import {
  AlertDialog,
  AlertDialogBody,
  AlertDialogContent,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogOverlay,
  Button,
} from "@chakra-ui/react";

export const DeleteNote = ({
  isOpen,
  onClose,
  cancelRef,
  currentId,
  onClickDelete,
  isDeleteAll,
}) => {
  return (
    <AlertDialog
      isOpen={isOpen}
      onClose={onClose}
      leastDestructiveRef={cancelRef}
    >
      <AlertDialogOverlay>
        <AlertDialogContent>
          <AlertDialogHeader>
            {isDeleteAll ? "  " : " "}
          </AlertDialogHeader>
          <AlertDialogBody>
            {isDeleteAll
              ? "  ?"
              : "  ?"}
          </AlertDialogBody>
          <AlertDialogFooter>
            <Button variant="outline" mr={4} ref={cancelRef} onClick={onClose}>
              
            </Button>
            <Button
              colorScheme="red"
              onClick={() => {
                onClose();
                isDeleteAll ? onClickDelete() : onClickDelete(currentId);
              }}
            >
              
            </Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialogOverlay>
    </AlertDialog>
  );
};
