import { Box, Button, Heading, Text, VStack } from "@chakra-ui/react";
import { useRef, useState } from "react";
import { Link } from "react-router-dom";

export const SpeedCheck = () => {
  const [isColor, setIsColor] = useState("purple.200");
  const [isState, setIsState] = useState("");
  const [isText, setIsText] = useState(
    "    !!"
  );
  const [desc, setDesc] = useState("  .");
  const [result, setResult] = useState([]);
  const clickTimer = useRef(null);
  const startTime = useRef(0);
  const endTime = useRef(0);
  const count = useRef(0);

  const handleStart = () => {
    if (isState === "") {
      setIsState("");
      setIsText("  !");
      setDesc("");
      setIsColor("red.300");

      clickTimer.current = setTimeout(() => {
        setIsState("!");
        setIsText("!");
        setDesc("");
        setIsColor("green.300");
        startTime.current = new Date();
      }, Math.floor(Math.random() * 2000) + 1000);
    } else if (isState === "") {
      clearTimeout(clickTimer.current);

      setIsState("  .");
      setIsText("   ! ");
      setDesc("   .");
      setIsColor("gray.300");
    } else if (isState === "  .") {
      setIsState("");
      setIsText("    !!");
      setDesc("  .");
      setIsColor("purple.200");
    } else if (isState === "3   .") {
      setResult([]);
      setIsState("");
      setIsText("    !!");
      setDesc("  .");
      setIsColor("purple.200");
    } else if (isState === "!") {
      endTime.current = new Date();

      setResult((prevResults) => {
        return [...prevResults, endTime.current - startTime.current];
      });

      setIsState("");
      setIsText("    !!");
      setDesc("  .");
      setIsColor("purple.200");

      count.current += 1;

      if (count.current >= 3) {
        setIsState("3   .");
        setIsText("   .");
        setDesc(
          `  : 273ms     \n   : 150ms ~ 160ms`
        );
        setIsColor("blue.200");
        count.current = 0;
      }
    }
  };
  const resAverage = () => {
    const average =
      result.length === 0 ? null : (
        <Text fontSize="24px" mt={4}>
             :{" "}
          {(result.reduce((acc, curr) => acc + curr) / result.length).toFixed(
            2
          )}{" "}
          ms
        </Text>
      );
    return average;
  };
  return (
    <>
      <Heading fontSize="20px" mb={4}>
         !  3!
      </Heading>
      <Box
        w="100%"
        h="250px"
        bg={isColor}
        borderRadius="10px"
        display="flex"
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
        lineHeight="28px"
        cursor="pointer"
        color="#fff"
        onClick={() => {
          handleStart();
        }}
      >
        <Heading fontSize="24px">{isState}</Heading>
        <Text>{isText}</Text>
        <Text whiteSpace="pre-line" textAlign="center">
          {desc}
        </Text>
      </Box>
      <VStack gap={2} mt={3}>
        {result.map((time, index) => (
          <Text key={index}>
            {index + 1}   :{" "}
            <span style={{ fontWeight: "600" }}>{time}</span> ms
          </Text>
        ))}
      </VStack>
      {resAverage()}
      <Box w="100%" mt={10}>
        <Link to="/menu">
          <Button
            w="100%"
            bg="#ffb6c1"
            color="#fff"
            fontSize="20px"
            fontWeight="700"
            _hover={{
              bg: "red.100",
            }}
            _active={{ bg: "red.200", transform: "scale(1.05)" }}
          >
             
          </Button>
        </Link>
      </Box>
    </>
  );
};
